@import url(../../node_modules/normalize.css/normalize.css);
* {
  box-sizing: border-box; }

body {
  font-family: "Helvetica Neue",Helvetica,"Microsoft Yahei",Arial,sans-serif;
  font-size: 16px;
  color: #333; }

input {
  outline: 0; }

.textbox {
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 7px 10px;
  display: inline-block;
  width: 100%;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075); }
  .textbox:focus {
    outline: 0; }

.hidden {
  display: none; }

.container {
  margin: 0 auto;
  padding: 0 15px; }
  @media screen and (min-width: 768px) {
    .container {
      width: 750px; } }
  @media screen and (min-width: 992px) {
    .container {
      width: 970px; } }
  @media screen and (min-width: 1200px) {
    .container {
      width: 1170px; } }

.markdown pre {
  background-color: #eee;
  padding: 10px;
  max-height: 500px;
  overflow: auto;
  border: none;
  font-size: 0.9em;
  line-height: 1.33; }

#editor {
  margin-top: 20px;
  width: 100%;
  max-width: 1600px; }

.editor-title {
  font-size: 18px; }
  .editor-title .textbox {
    line-height: 1.33; }

/* Style for Tag Input */
.tag-input {
  position: relative;
  margin: 20px 0;
  cursor: text; }
  .tag-input input {
    line-height: 1.33;
    padding: 2px 0; }

.tag {
  display: inline-block;
  padding: 0.2em 0.4em;
  margin-right: .3em;
  color: steelblue;
  background-color: lightblue;
  border-radius: .2em;
  font-size: .8em;
  font-weight: 300; }

.tag-input input {
  border: none;
  border-radius: 1em;
  margin-left: .2em;
  font-size: .8em; }
  .tag-input input:focus {
    outline: 0; }

.tag-input .hidden {
  position: absolute;
  opacity: 0;
  left: 0; }

/* Style for Editor */
.md-panel {
  display: block;
  position: relative;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 14px; }

.md-toolbar {
  position: absolute;
  width: 100%;
  height: 37px;
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  box-shadow: 0 0 1px #ccc;
  z-index: 10; }

.md-editor {
  width: 50%;
  height: auto; }

.md-editor textarea {
  border-style: none;
  resize: none;
  outline: 0;
  height: 100%;
  min-height: 540px;
  width: 100%;
  margin-top: 47px;
  padding: 0 15px;
  line-height: 1.33; }

.md-preview {
  position: absolute;
  width: 50%;
  height: 100%;
  left: 50%;
  top: 0;
  background-color: #f6f6f6;
  padding: 45px 20px 20px 20px;
  overflow: auto; }

.md-spliter {
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  border-left: 1px solid #ccc;
  z-index: 105; }
